@import "src/styles/statement";

#home {

  /*蒙版*/
  .mask{
    background-color: rgba(0,0,0,0.4);
    width: 100vw;
    height: 100vh;
    z-index: -9;
    position: fixed;
  }
  //背景图
  .backGround {
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: -10;
    transition-duration: 0.5s;
    background: url("../../images/home/coronavirus-4937226_1920.jpg") no-repeat center;
  }
}