@import "src/styles/statement";

#Hello {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
  user-select: none;


  .body {
    width: 1200px;
    margin: 0 auto;
    .text {
      font-size: 35px;
      margin-top: 250px;
      text-align: center;
      h1{
        color: #ffffff;
      }
      h2 {
        color: #fff;
      }
    }
    .finger {
      position: absolute;
      top: 35px;
      left: 400px;
      font-size: 60px;
      color: white;
    }
    .button {
      position: relative;
      a {
        display: inline-block;
        margin: 60px 500px;
      }
    }
  }

  @include styleMobile{

    .body {
      width: 100vw;
      .text {
        font-size: conversionPX(16);
        margin-top: conversionVH(200);
      }
      .finger {
        top: conversionVH(55);
        left: conversionPX(50);
        font-size: conversionPX(30);
      }
      .button {
        button {
          width: conversionPX(170);
          height: conversionPX(40);
          font-size: conversionPX(16);
        }
        a {
          margin: conversionVH(60) conversionPX(100);
        }
      }
    }
  }
}